/** @jsx jsx */
import { jsx } from "theme-ui";
import algoliasearch from "algoliasearch/lite";
import {
  InstantSearch,
  Hits,
  RefinementList,
  Pagination,
  connectStateResults,
} from "react-instantsearch-dom";
import { rem } from "@src/theme";
import { Link } from "gatsby";
import TypeRefinementList from "@components/TypeRefinementList";
import Container from "@components/Container";
import ScrollContainer from "@components/ScrollContainer";
import ResourceCard from "@components/ResourceCard";
import EyeOff from "@src/components/icons/eye";
import CollectionHeader from "@src/components/CollectionHeader";
import Layout from "@src/components/Layout";
const searchClient = algoliasearch(
  "ED9HZMT8Z4",
  "65441d4adde5fd0d03a0b683d232262a"
);

interface ComponentProps {
  defaultRefinement?: Array<string>;
  refinementType?: string;
  defaultQuery?: string;
  isSearch: Boolean;
  heading: string;
  description?: string;
}
export default function({
  defaultRefinement,
  isSearch,
  heading,
  description,
  refinementType,
  defaultQuery,
}: ComponentProps) {
  return (
    <Layout sticky={true} title={heading}>
      <InstantSearch
        searchClient={searchClient}
        // indexName={process.env.ALGOLIA_INDEX || "resources-csi-01-stg"}
        indexName={"resources-csi-01-stg"}
      >
        <CollectionHeader
          defaultQuery={defaultQuery}
          title={heading}
          description={description}
          isSearch={isSearch}
          bgType={isSearch ? "search" : "user"}
        />

        <div
          sx={{
            py: [rem(20), rem(40)],
          }}
        >
          <ScrollContainer>
            <TypeRefinementList attribute="type" />
          </ScrollContainer>
        </div>

        {refinementType && (
          <div
            sx={{
              display: "none",
            }}
          >
            <RefinementList
              attribute={refinementType}
              defaultRefinement={defaultRefinement}
            />
            <RefinementList attribute="enabled" defaultRefinement={[true]} />
          </div>
        )}

        <Container>
          <Results>
            <section
              sx={{
                ".ais-Hits-list": {
                  display: "grid",
                  gridTemplateColumns: [
                    "repeat(1,1fr)",
                    "repeat(2,1fr)",
                    "repeat(3,1fr)",
                  ],
                  gridColumnGap: ["0.85rem", null, "3.00rem"],
                  gridRowGap: ["0.85rem", null, "3.00rem"],
                },
              }}
            >
              <Hits hitComponent={ResourceCard} />
            </section>
          </Results>
        </Container>

        <div className="pagination">
          <Pagination />
        </div>
      </InstantSearch>
    </Layout>
  );
}

const Results = connectStateResults(({ searchResults, children }) =>
  searchResults && searchResults.nbHits !== 0 ? (
    children
  ) : (
    <section
      sx={{
        textAlign: "center",
      }}
    >
      <EyeOff />
      <h1
        sx={{
          mt: 0,
          mb: rem(10),
          fontSize: rem(20),
        }}
      >
        We can't SEE any matching evidence
      </h1>
      <p
        sx={{
          mt: 0,
        }}
      >
        Try searching for a similar term or <Link to="/">explore</Link> instead
      </p>
    </section>
  )
);
