/** @jsx jsx */
import { jsx } from "theme-ui";
import { connectRefinementList } from "react-instantsearch-dom";
import { rem } from "@src/theme";
import * as _ from "lodash";
const RefinementList = ({ items, currentRefinement, refine }) => {
  const refinements = [
    "Insight",
    "Statistic",
    "Report",
    "Academic article",
    "Infographic",
    "Tool",
    "Video",
  ];

  let sortedItems = _.sortBy(items, function(obj) {
    return _.indexOf(refinements, obj.label);
  });

  return (
    <ul
      sx={{
        display: "flex",
        flexWrap: ["nowrap", null, "wrap"],
        fontSize: [rem(14), null, rem(16)],
        li: {
          fontWeight: 600,
          mr: "20px",
          whiteSpace: "nowrap",
          "&:last-child": {
            pr: "4%",
          },
        },
        overflowX: "scroll",
        a: {
          textDecoration: "none",
        },
      }}
    >
      <li key="all">
        <a
          href="#"
          sx={{
            color: !currentRefinement.length ? "darkGrey" : "#616161",
            fontWeight: !currentRefinement.length ? "bold" : "",
            borderBottom: !currentRefinement.length
              ? "2px solid #616161"
              : "none",
          }}
          onClick={(event) => {
            event.preventDefault();
            refine("");
          }}
        >
          All
        </a>
      </li>

      {sortedItems.map((item) => (
        <li key={item.label}>
          <a
            href="#"
            sx={{
              fontWeight: item.isRefined ? "bold" : "",
              borderBottom: item.isRefined ? "2px solid #616161" : "none",
              color: !currentRefinement.length ? "darkGrey" : "#616161",
            }}
            onClick={(event) => {
              event.preventDefault();
              refine(item.value);
            }}
          >
            {item.label}
          </a>
        </li>
      ))}
    </ul>
  );
};

const CustomRefinementList = connectRefinementList(RefinementList);

export default CustomRefinementList;
